// COLORS
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-text:rgba(118, 134, 159, 0.8);
  --color-btn:#5CA35B;
  --color-btn-2:#212A38;
  --color-price: #6A7395;
  --color-price-2:#6A9590;
  --color-price-3:#95936A;
  --color-test:#717E92;;
  --color-bg:#2D3746;
  --color-bg-2:#181F29;
  --color-card:#273140;



  // Шрифты
  --font-family: "SFUIDisplay", "Mont", sans-serif;
  --text: 500 18px / 27px var(--font-family);
  --title: 700 62px / 63px var(--font-family);
  --title-2: 700 42px / 50px var(--font-family);
  --title-3: 700 40px / 50px var(--font-family);
  --text-2: 400 26px / 34px var(--font-family);
  --text-3: 400 24px / 36px var(--font-family);
  --text-4: 400 20px / 29px var(--font-family);
  --text-title: 700 28px / 36px var(--font-family);
  --text-title-3: 700 18px / 23px var(--font-family);
  --text-title-1: 500 18px / 26px var(--font-family);
  --text-title-2: 500 16px / 26px var(--font-family);
  --text-title-4: 500 12px / 17px var(--font-family);

}
