html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "SFUIDisplay", sans-serif;
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.list-reset{
  margin: 0;
  padding: 0;
  list-style: none;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1134px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.h1,.h2,.h3,.h4,.h5,.h6{
  padding: 0;
  margin: 0;
}

.title{
  padding: 0;
  margin: 0;
  font: var(--title-2);
  color: var(--color-white);
}

.text{
  padding: 0;
  margin: 0;
  font: var(--text-4);
  color: var(--color-price);
}
